.footer {
  min-height: 80px;
  background-color: #3D4050;
  font-size: 13px;
}
.footer__wrap {
  width: 1170px;
  margin: auto;
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .footer__wrap {
    width: auto;
  }
}
.footer__links {
  padding-top: 34px;
  padding-left: 0;
}
.footer__links__qiwi, .footer__links__ym, .footer__links__wm {
  color: #C3C3C3;
}
.footer__links__qiwi, .footer__links__mail {
  border-left: 1px solid #C3C3C3;
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .footer__links__qiwi, .footer__links__mail {
    border: none;
  }
}
.footer__links__qiwi {
  background-image: url("../img/qiwi.webp");
  background-position: 40px 0;
  background-repeat: no-repeat;
  padding-left: 70px;
  margin-left: 40px;
}
.footer__links__ym {
  background-image: url("../img/ym.webp");
  background-position: 7px 0;
  background-repeat: no-repeat;
  padding-left: 30px;
  margin-left: 30px;
}
.footer__links__wm {
  background-image: url("../img/wm.webp");
  background-position: 5px 0;
  background-repeat: no-repeat;
  padding-left: 30px;
  margin-left: 30px;
}
.footer__links__mail {
  background-image: url("../img/mail-logo.webp");
  background-position: 37px 0;
  background-repeat: no-repeat;
  padding-left: 70px;
  margin-left: 40px;
}
.footer__links__vk {
  background-image: url("../img/vk.webp");
  background-position: 2px 0;
  background-repeat: no-repeat;
  padding-left: 30px;
  margin-left: 30px;
}
.footer__links > * {
  display: inline-block;
  padding-top: 5px;
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .footer__links > * {
    margin-bottom: 20px;
    margin-left: 20px;
  }
}
@media all and (min-width: 121px) and (max-width: 640px) {
  .footer__links > * {
    display: block;
    text-align: left;
    margin-left: 20px;
    padding-left: 38px;
    background-position: 0;
  }
}
.footer__links > * a {
  color: #fff;
}
.footer__links > * a:hover {
  color: #42A9ED;
}
.footer__links__license {
  color: #fff;
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .footer__links__license {
    display: block;
    text-align: center;
    margin-bottom: 20px;
  }
}
@media all and (min-width: 121px) and (max-width: 640px) {
  .footer__links__license {
    padding-left: 0;
  }
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .footer__links {
    padding: 35px 35px 25px 10px;
    text-align: center;
  }
}

.header {
  background-color: rgba(16, 16, 29, 0.9019607843);
  color: #fff;
  width: 100%;
  position: fixed;
  left: 50%;
  top: 0;
  min-height: 100px;
  -webkit-transform: translate(-50%);
  -moz-transform: translate(-50%);
  -ms-transform: translate(-50%);
  -o-transform: translate(-50%);
  transform: translate(-50%);
  z-index: 9999;
}
@media all and (min-width: 121px) and (max-width: 1170px) {
  .header {
    position: inherit;
  }
}
.header__logo {
  text-transform: uppercase;
